// --- „Åì„Åì„Åã„ÇâÊó¢Â≠ò„ÅÆÈñ¢Êï∞ÂÆöÁæ© (page.tsx„Åã„Çâ„Ç≥„Éî„Éº) ---
function calculateDistance(lat1: number, lng1: number, lat2: number, lng2: number): number {
    const R = 6371000;
    const dLat = (lat2 - lat1) * (Math.PI / 180);
    const dLng = (lng2 - lng1) * (Math.PI / 180);
    const a =
        Math.sin(dLat / 2) * Math.sin(dLat / 2) +
        Math.cos(lat1 * (Math.PI / 180)) * Math.cos(lat2 * (Math.PI / 180)) * Math.sin(dLng / 2) * Math.sin(dLng / 2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    return R * c;
}

async function getAddressFromCoords(lat: number, lng: number) {
    try {
        const res = await fetch(`https://nominatim.openstreetmap.org/reverse?format=json&lat=${lat}&lon=${lng}&zoom=18&addressdetails=1`);
        if (!res.ok) throw new Error('Address fetch failed');
        const data = await res.json();
        const addr = data.address;
        const pref = addr.province || addr.state || '';
        const city = addr.city || addr.town || addr.village || '';
        const suburb = addr.suburb || addr.quarter || addr.neighbourhood || '';
        const road = addr.road || '';
        const fullAddress = `${pref}${city}${suburb}${road}`;
        let cityCode = null;
        if (city && CITY_NAME_TO_CODE[city]) {
            cityCode = CITY_NAME_TO_CODE[city];
        }
        return { fullAddress, cityCode, cityName: city };
    } catch (e) {
        console.error(e);
        return { fullAddress: '', cityCode: null, cityName: '' };
    }
}

function MapControllerLogic({ setCenter, setZoom }: { setCenter: (pos: { lat: number; lng: number }) => void; setZoom: (z: number) => void }) {
    const searchParams = useSearchParams();
    useEffect(() => {
        const latParam = searchParams.get('lat');
        const lngParam = searchParams.get('lng');
        const zoomParam = searchParams.get('zoom');
        if (latParam && lngParam) {
            const lat = parseFloat(latParam);
            const lng = parseFloat(lngParam);
            if (!isNaN(lat) && !isNaN(lng)) {
                setCenter({ lat, lng });
                if (zoomParam) {
                    const z = parseInt(zoomParam, 10);
                    if (!isNaN(z)) setZoom(z);
                }
            }
        }
    }, [searchParams, setCenter, setZoom]);

    // ‚Äª LP„ÅÆÂ†¥Âêà„ÅØ„É≠„Éº„Ç´„É´„Çπ„Éà„É¨„Éº„Ç∏„Åã„Çâ„ÅÆÂæ©Â∏∞„Çà„Çä„ÇÇ„ÄÅLP„ÅÆ„Éá„Éï„Ç©„É´„Éà‰ΩçÁΩÆ„ÇíÂÑ™ÂÖà„Åó„Åü„ÅÑÂ†¥Âêà„Åå„ÅÇ„Çä„Åæ„Åô„Åå„ÄÅ
    // „Åì„Åì„Åß„ÅØ„ÄåURL„Éë„É©„É°„Éº„Çø„Åå„ÅÇ„Çå„Å∞Âæì„ÅÜ„ÄÅ„Å™„Åë„Çå„Å∞„Çπ„Éà„É¨„Éº„Ç∏„Äç„Å®„ÅÑ„ÅÜÊó¢Â≠ò„É≠„Ç∏„ÉÉ„ÇØ„ÅÆ„Åæ„Åæ„Å´„Åó„Åæ„Åô„ÄÇ
    useEffect(() => {
        const currentParams = new URLSearchParams(window.location.search);
        if (currentParams.has('lat') && currentParams.has('lng')) return;

        const savedPos = localStorage.getItem(STORAGE_KEY);
        if (savedPos) {
            try {
                const parsed = JSON.parse(savedPos);
                if (parsed.lat && parsed.lng) {
                    setCenter({ lat: parsed.lat, lng: parsed.lng });
                    if (parsed.zoom) setZoom(parsed.zoom);
                }
            } catch (e) {
                console.error(e);
            }
        }
    }, [setCenter, setZoom]);
    return null;
}
